.login {
    height: 100vh;
    background-position: center;
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
    .lgbg {
        width: 1200px;
        height: 720px;
        .part {
            width: 364px;
            height: 580px;
            padding: 140px 20px 0 20px;
            background-color: white;
            margin-left: auto;
            .title {
                margin-bottom: 50px;
                .logo {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                }
                h1 {
                    text-align: center;
                    color: rgb(64, 140, 202);
                    margin-top: 40px;
                }
            }
        }
    }
}
